.HomePageWrap {
  padding-top: 1rem;
}
.height200x {
  padding-top: 2rem;
}
.postList {
  padding-bottom: 1rem;
}
.postList > div > div:nth-child(1) {
  border-top: 0;
}
.header {
  background-color: #fff;
  height: 2rem;
  width: 100%;
}
.searchBar {
  position: fixed;
  top: 0;
  left: 0;
  height: 1rem;
  width: 100%;
  z-index: 10;
}
.listView > div > div:nth-child(2) {
  padding: 0;
}
.listitem:after {
  content: '';
  height: 0.2rem;
  width: 100%;
  display: block;
  background-color: #f5f5f9;
}



