.introduceContent {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0.4rem;
  background: #fff;
  margin-top: 0.2rem;
  font-size: 0.28rem;
  line-height: 0.4rem;
  color: #777;
  position: relative;
}
.introduceContent .title {
  color: #4A4A4A;
  width: 100%;
  height: 0.42rem;
  line-height: 0.42rem;
  font-size: 0.3rem;
}
.introduceContent .mesCon {
  margin-top: 0.1rem;
  overflow: hidden;
  padding-bottom: 0;
  -webkit-transition: 0.25s;
  -o-transition: 0.25s;
  transition: 0.25s;
}
.introduceContent .moreMesCon {
  margin-top: 0.1rem;
  padding-bottom: 0.5rem;
}
.introduceContent .gtMore {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 0.8rem;
  background: -webkit-gradient(linear, left bottom, left top, from(#ffffff), to(rgba(255, 255, 255, 0.5)));
  background: -webkit-linear-gradient(bottom, #ffffff, rgba(255, 255, 255, 0.5));
  background: -o-linear-gradient(bottom, #ffffff, rgba(255, 255, 255, 0.5));
  background: linear-gradient(to top, #ffffff, rgba(255, 255, 255, 0.5));
  text-align: center;
  overflow: hidden;
}
.introduceContent .gtMore img {
  width: 0.5rem;
  margin-top: 0.2rem;
}
.rejectThis {
  display: block;
  width: 100%;
  color: #129BF8;
  font-size: 0.28rem;
  margin-top: 0.1rem;
  text-align: right;
}



