.advertising {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 1000;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.hidden {
  display: none !important;
}
.close {
  margin-bottom: 0.08rem;
  width: 100%;
  text-align: right;
  padding-right: 0.68rem;
}
.close img {
  width: 0.56rem;
}
.adbody {
  width: 5.2rem;
}
.adbody img {
  width: 100%;
  border-radius: 0.18rem;
}
.downloadBtn {
  margin-top: 0.38rem;
  color: #fff;
  font-size: 0.32rem;
  background-color: #ff5001;
  width: 4rem;
  height: 0.92rem;
  border-radius: 1rem;
  line-height: 0.92rem;
}
.AdTopWrap {
  height: 1rem;
  padding: 0 0.2rem;
  background: rgba(0, 0, 0, 0.85);
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
}
.AdTopWrap .logo {
  width: 0.72rem;
  height: 0.72rem;
  padding-right: 0.1rem;
}
.AdTopWrap .logo img {
  width: 100%;
}
.AdTopWrap .keyword {
  -ms-flex: 1 1;
      flex: 1 1;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  font-size: 0.28rem;
}
.AdTopWrap .keyword p:nth-child(2) {
  font-size: 0.24rem;
  color: rgba(200, 199, 204, 0.69);
  letter-spacing: 0.003rem;
}
.AdTopWrap .btn {
  width: 1.68rem;
  height: 0.68rem;
  border-radius: 0.68rem;
  font-size: 0.26rem;
  text-align: center;
  line-height: 0.68rem;
}



