.CompanyDetailWrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
  -webkit-overflow-scrolling: touch;
}
.connent {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 0;
  overflow-y: scroll;
}
.registerWrap {
  height: 1.2rem;
  background: #333333;
  padding: 0 0.6rem;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  position: relative;
}
.registerWrap .left {
  -ms-flex: 1 1;
      flex: 1 1;
}
.registerWrap .left .title {
  font-size: 0.32rem;
}
.registerWrap .left .des {
  font-size: 0.24rem;
  color: #C8C7CC;
}
.registerWrap .regist_btn {
  width: 1.6rem;
  height: 0.6rem;
  line-height: 0.6rem;
  font-size: 0.24rem;
  letter-spacing: 0.01rem;
  text-align: center;
  border-radius: 1rem;
  background-image: -webkit-gradient(linear, right top, left top, color-stop(23%, #FF4F00), to(#FF7214));
  background-image: -webkit-linear-gradient(right, #FF4F00 23%, #FF7214 100%);
  background-image: -o-linear-gradient(right, #FF4F00 23%, #FF7214 100%);
  background-image: linear-gradient(-90deg, #FF4F00 23%, #FF7214 100%);
}
.registerWrap .close {
  position: absolute;
  right: 0;
  top: -0.32rem;
  width: 0.46rem;
  height: 0.36rem;
  background: url("../../static/close_reg.png") center no-repeat;
  background-size: cover;
}



