.root {
  height: 100%;
}
.wrap {
  height: 0;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  background-color: #fff;
  margin-left: 0 !important;
}
.wrap > div {
  margin: 0.4rem 0.2rem;
}
.list {
  margin-top: 0.48rem;
}
.list .authCode {
  width: 60%;
}
.list :global .am-list-item .am-list-line .am-list-extra {
  text-align: left;
}
.list :global .am-input-item > .am-input-control > input {
  text-align: left;
}
.emailCode {
  position: absolute;
  right: 0;
  bottom: 1.34rem;
}
.nav :global .am-navbar-right span {
  padding: 0.24rem;
  margin-right: -0.24rem;
}



