.SearchEndWrap {
  padding-top: 2rem;
}
.top {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: #f5f5f9;
}
.searchCondition {
  height: 0.8rem;
  margin-bottom: 0.22rem;
}
.listItem:after {
  content: '';
  height: 0.2rem;
  width: 100%;
  display: block;
  background-color: #f5f5f9;
}
.listBox > div > div:nth-child(1) {
  border-top: 0;
}



