.content {
  width: 100%;
  background-color: #fff;
  padding: 0.14rem 0.4rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  min-height: 0.72rem;
  position: relative;
  overflow: hidden;
  margin-top: 0.4rem;
}
.content:after {
  clear: both;
  content: "";
  display: block;
}
.content .inputCon {
  width: 86%;
  overflow-x: hidden;
}
.content .inputCon .input {
  resize: none;
  font-size: 0.26rem;
  line-height: 0.36rem;
  word-wrap: break-word;
  white-space: pre-wrap;
  width: 100%;
  padding: 0.18rem 0 0.18rem 0.2rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.content .inputCon textarea {
  height: 0.72rem;
  background-color: #E2E2E2;
  border: 0;
  border-radius: 0.16rem;
}
.content .inputCon pre {
  visibility: hidden;
  position: absolute;
  z-index: -1;
  min-height: 0.72rem;
  max-height: 1.44rem;
}
.content .inputCon ::-webkit-input-placeholder {
  color: #9B9B9B;
}
.content .sendCon {
  float: right;
  font-size: 0.32rem;
  line-height: 0.44rem;
  text-align: right;
  position: absolute;
  right: 0.4rem;
  bottom: 0.3rem;
  color: #FF7214;
}



