.ForMassageWrap {
  background-color: #fff;
  padding: 0.4rem 0.4rem 0.6rem 0.4rem;
  color: #777777;
  font-size: 0.28rem;
}
.ForMassageWrap .title {
  color: #4A4A4A;
  font-size: 0.32rem;
}
.ForMassageWrap .container {
  background-color: #FEE7D7;
  padding: 0.4rem 0.24rem;
  margin: 0.4rem 0;
  border-radius: 0.16rem;
}
.ForMassageWrap .container .conntent {
  line-height: 1.6;
  max-height: 1.8rem;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  -webkit-line-clamp: 4;
}
.ForMassageWrap .container .btn {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 0.2rem;
  width: 100%;
}
.ForMassageWrap .container .btn img {
  width: 0.24rem;
  margin-right: 0.24rem;
}
.ForMassageWrap .comapnyNmae {
  text-align: right;
  margin-bottom: 0.2rem;
}
.ForMassageWrap .data {
  text-align: right;
}
.more {
  max-height: none !important;
  height: auto !important;
  overflow: auto !important;
  display: block !important;
}
.up {
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
}
.down {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
}



