.top {
  padding-top: 0.5rem;
  padding-bottom: 0.4rem;
  text-align: center;
  background-color: #fff;
}
.top img {
  width: 1.6rem;
  height: 1.6rem;
  margin-bottom: 0.24rem;
}
.top .version {
  font-size: 0.28rem;
  color: #777777;
  margin-bottom: 0.4rem;
}
.top .introduce {
  padding: 0 0.4rem;
  font-size: 0.26rem;
  color: #4A4A4A;
  letter-spacing: 0.02rem;
  line-height: 0.4rem;
  text-align: left;
}
.middle {
  margin-top: 0.2rem;
  padding: 0.4rem 0.4rem;
  background-color: #fff;
}
.middle .item {
  text-align: left;
  height: 0.8rem;
  border-bottom: 0.01rem solid rgba(200, 199, 204, 0.5);
}
.middle .item .title {
  font-size: 0.26rem;
  color: #5C5C5C;
  letter-spacing: 0.02rem;
  line-height: 0.8rem;
}
.middle .item .value {
  color: #2f8df8;
}
.middle .item .font {
  color: #000000;
}



