.content {
  width: 100%;
  height: 2.02rem;
  background: #fff;
  margin-top: 0.2rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0 0.6rem 0.3rem;
  overflow: hidden;
}
.content .top {
  width: 100%;
  border-bottom: 0.01rem solid #C8C7CC;
  line-height: 0.85rem;
  margin-top: 0.17rem;
  height: 0.85rem;
  color: #4A4A4A;
  font-size: 0.3rem;
  background: url(../../static/contend@3x.png) left center / 0.44rem 0.44rem no-repeat;
  text-indent: 2em;
}
.content .bottom {
  width: 100%;
}
.content .bottom input {
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  font-size: 0.26rem;
  outline: none;
  color: #129BF8;
}
:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #C8C7CC;
}
::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #C8C7CC;
}
input:-ms-input-placeholder {
  color: #C8C7CC;
}
input::-webkit-input-placeholder {
  color: #C8C7CC;
}



