.hotelEntry {
  width: 100%;
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0.3rem 0.4rem;
  display: block;
  background-color: #fff;
  margin-top: 0.2rem;
}
.hotelEntry:after {
  content: '';
  clear: both;
  display: block;
}
.hotelEntry .left {
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 1.2rem;
  float: left;
}
.hotelEntry .center {
  width: calc(100% - 2rem);
  margin-left: 0.4rem;
  float: left;
}
.hotelEntry .center .hotelName {
  width: 100%;
  margin-top: 0.25rem;
  font-size: 0.3rem;
  color: #333;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
}
.hotelEntry .center .hotelSize {
  width: 2.8rem;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  font-size: 0.24rem;
  color: #AAA;
  white-space: nowrap;
  margin-top: 0.08rem;
}
.hotelEntry .right {
  float: right;
}
.hotelEntry .right img {
  width: 0.28rem;
  margin-top: 0.45rem;
}



