.content {
  width: 100%;
  background: #fff;
  height: 4.12rem;
  margin-top: 0.2rem;
  padding: 0.2rem 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.content textarea {
  width: 100%;
  height: 100%;
  border: none;
  font-size: 0.26rem;
  line-height: 0.36rem;
  text-indent: 2em;
}
:-moz-placeholder {
  /* Mozilla Firefox 4 to 18 */
  color: #C8C7CC;
}
::-moz-placeholder {
  /* Mozilla Firefox 19+ */
  color: #C8C7CC;
}
textarea:-ms-input-placeholder {
  color: #C8C7CC;
}
textarea::-webkit-input-placeholder {
  color: #C8C7CC;
}



