.MassagePageWrap {
  height: 100%;
}
.tabsCon :global .am-tabs-bar {
  background-color: #FF4F00;
}
.tabsCon :global .am-tabs-tab {
  background-color: #FF4F00;
  color: #fff;
  font-weight: normal;
  font-size: 0.34rem;
  opacity: .8;
}
.tabsCon :global .am-tabs-tab-active {
  opacity: 1;
}
.tabsCon :global .am-tabs-ink-bar {
  opacity: 0;
}
.tabsCon :global .am-badge {
  line-height: inherit;
}



