.CarouselsWrap {
  width: 100%;
  min-height: 2rem;
}
.CarouselsWrap .carousel {
  width: 100%;
}
.CarouselsWrap .aLink {
  overflow: hidden;
  text-align: center;
}
.CarouselsWrap .aLink img {
  width: 100%;
}
.CarouselsWrap:after {
  content: '';
  height: 0.2rem;
  width: 100%;
  display: block;
  background-color: #f5f5f9;
}



