.jobCard {
  margin-top: 0.2rem;
  min-height: auto;
  padding-bottom: 0.4rem;
}
.cardHeader {
  font-size: 0.32rem;
  color: #333;
}
.cardHeader .hot {
  color: #fff;
  font-size: 0.2rem;
  line-height: 0.3rem;
  background: #FF7214;
  display: inline-block;
  height: 0.3rem;
  padding: 0 0.15rem;
  border-radius: 0.15rem;
  margin-left: 0.1rem;
  vertical-align: 0.02rem;
}
.cardCenter {
  border: none ;
  min-height: 0.45rem;
  padding: 0rem 0.3rem;
}
.inner {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
