  height: 0.4rem;
  line-height: 0.4rem;
  font-size: 0.24rem;
  padding-left: 0.3rem;
}
.otherNeed {
  font-size: 0.24rem;
  color: #777;
  margin: 0.1rem 0 0.2rem 0.3rem;
}
.welfare {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.welfare li {
