  height: 0.4rem;
  border-radius: 0.16rem;
  background: #F0F0F0;
  color: #777;
  font-size: 0.24rem;
  text-align: center;
  line-height: 0.4rem;
  margin: 0.15rem 0.15rem 0 0;
}



