.UserPageWrap {
  padding-bottom: 1rem;
}
.top {
  background-color: #FF4F00 !important;
  height: 2.6rem;
  padding: 0.3rem 0.4rem;
}
.hasLogin {
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  height: 100%;
}
.hasLogin .left {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: auto;
  -ms-flex: 1 1;
      flex: 1 1;
  font-size: 0.32rem;
  overflow: hidden;
}
.hasLogin .left .imgBox {
  width: 1.2rem;
  height: 1.2rem;
  margin-left: 0.2rem;
  margin-right: 0.2rem;
  position: relative;
  background-size: cover;
  border-radius: 50%;
  overflow: hidden;
}
.hasLogin .left img {
  width: 1.2rem;
}
.hasLogin .left .username {
  width: auto;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  white-space: nowrap;
}
.hasLogin .right {
  font-size: 0.24rem;
  width: 1.6rem;
}
.hasLogin .right .btn {
  background: rgba(51, 51, 51, 0.2);
  border-radius: 0.16rem;
  padding: 0.08rem 0.14rem;
}
.hasLogin .right .btn img {
  width: 0.3rem;
}
.resumeBox,
.bottom,
.middleBox {
  margin: 0.2rem 0;
}
@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  50% {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.addFrames {
  -webkit-animation: rotate 2s infinite linear ;
          animation: rotate 2s infinite linear ;
}
.headPro {
  width: 100%;
  height: 100%;
  font-size: 0;
  opacity: 0;
}



