.content {
  width: 100%;
  padding: 0 0.4rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin-top: 0.4rem;
  overflow: hidden;
}
.content:after {
  display: block;
  content: "";
  clear: both;
}
.content .head {
  width: 0.64rem;
  height: 0.64rem;
  border-radius: 50%;
  float: left;
  overflow: hidden;
  display: -ms-flexbox;
  display: flex;
  border: 0.01rem solid #d2d0d0;
  algin-item: center;
}
.content .head img {
  width: 100%;
}
.content .message {
  padding: 0.16rem 0.2rem;
  float: left;
  margin: 0 0.2rem;
  background-color: #fff;
  line-height: 0.34rem;
  font-size: 0.28rem;
  color: #777;
  border-radius: 0.16rem;
  max-width: 5rem;
  margin-top: 0.06rem;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.right .message {
  float: right;
  background-color: #FEE7D7 ;
}
.right .head {
  float: right;
}



