.articleWrap {
  background-color: #fff;
  padding: 0.5rem 0.4rem 0.3rem;
  margin-top: 0.2rem;
  font-size: 0.28rem;
}
.title {
  color: #4A4A4A;
  font-size: 0.3rem;
  margin-bottom: 0.16rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}
.titleImg {
  width: 0.34rem;
  height: 0.4rem;
  margin-right: 0.1rem;
}
.article {
  height: 2.2rem;
  max-height: 2.2rem;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  -webkit-line-clamp: 5;
  line-height: 1.6;
  color: #777777;
}
.backArticle {
  font-size: 0.28rem !important;
}
.moreArticle {
  max-height: none;
  height: auto;
  line-height: 1.6;
  color: #777777;
}
.report {
  font-size: 0.24rem;
  color: #129BF8;
  letter-spacing: 0;
  text-align: right;
  margin-top: 0.2rem;
}
.more {
  text-align: center;
  margin-top: 0.34rem;
}
.more img {
  width: 0.34rem;
  height: 0.34rem;
}
.shangle {
  -webkit-transform: rotate(0deg);
      -ms-transform: rotate(0deg);
          transform: rotate(0deg);
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}
.xiala {
  -webkit-transform: rotate(180deg);
      -ms-transform: rotate(180deg);
          transform: rotate(180deg);
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}



